    setCarouselItems = () =>{
      setTimeout(function(){ 
      var i=0;
      var j=0;
      var carouselitemss=[];
      var carouselitemssub=[];
      this.state.channels.forEach(data => {
        carouselitemssub.splice(j,0, {
          id: data.id,
          denumire: data.denumire,
          adresa: data.adresa,
          adresatwo: data.adresatwo,
          adresathree: data.adresathree,
          category: data.category,
          description: data.description,
          subcategory: data.subcategory,
          image: data.image,
          year: data.year,
          imdb: data.imdb,
          status: data.status
        })
        
        j=j+1;
        if(j===4){
          carouselitemss.splice(i,0,{carouselitemssub});
          carouselitemssub=[];
          i=i+1;
          j=0;
        }
        this.setState({
          carouselitems: carouselitemss
        });
      })
      //setTimeout(function(){ 
      //  console.log(JSON.stringify(this.state.carouselitems));
      //}.bind(this), 200);
      }.bind(this), 1200);
    }

